* {
  box-sizing: border-box;
}
body {
  margin: 0;
  background-color: #23252c;
  font-family: "Inter", sans-serif;
}
.main {
  max-width: 317px;
  margin-top: 44px;
  margin-bottom: 44px;
  margin-left: auto;
  margin-right: auto;
  color: white;
  background-color: #1a1b21;
  border-radius: 10px;
}
.profile-pic {
  width: 317px;
  height: 317px;
}

.name {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  text-align: center;
  margin-top: 21px;
  margin-bottom: 0;
}

.post {
  font-weight: 400;
  font-size: 12.8px;
  line-height: 15px;
  text-align: center;
  color: #f3bf99;
  margin-top: 2px;
  margin-bottom: 0;
}
.website {
  display: block;
  margin-top: 9px;
  font-size: 10.24px;
  line-height: 150%;
  text-align: center;
  color: #f5f5f5;
  text-decoration: none;
}

.email-btn {
  margin-top: 16px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 9px 13px 9px 11px;
  gap: 8px;
  background: #ffffff;
  border: 1px solid #d1d5db;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
  border-radius: 6px;
  width: 247px;
  height: 34px;
  margin-left: auto;
  margin-right: auto;
}
.mail-icon-container{
    display: flex;
}
.email-icon {
  width: 16px;
  height: 16px;
    align-self: auto;
}
.email-text {
  margin: 0;
  font-size: 14px;
  line-height: 16px;
  color: #374151;
align-self: auto;
}

/* about section */
.about,.interest{
    padding: 0 35px;
}

.about-tag,.interest-tag{
    font-weight: 700;
font-size: 16px;
line-height: 150%;
margin-top: 32px;
margin-bottom: 0;
}

.about-text,.interest-text{
    margin-top: 5px;
font-weight: lighter;
font-size: 10.24px;
line-height: 150%;
}
.interest{
    margin-top: 25px;
      margin-bottom: 40px;
}
.interest-tag{
    margin-top: 0;
}

/* footer */

.footer{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 24px;
    height: 64px;
    background-color: #161619;

}
.footer-icon{
    width: 25px;
    height: 25px;
}